import React from "react";
import Button from "./components/Button";
import Card from "./components/Card";
import Input from "./components/Input";
import "./Hero.css";

const Hero = () => {
  return (
    <div className="hero">
      <div className="hero-container">
        <div className="hero-contents">
          <div className="hero-text">
            <h1>
              뭔가 멋있는
              <br />
              카피문구
            </h1>
            <p>
              Consequat tempor ad Lorem in veniam ipsum ea. Culpa ad id velit
              eiusmod. Qui Lorem elit mollit consequat. Sint officia enim ad ex
              veniam amet laborum duis magna et duis.
            </p>
          </div>
          <div className="hero-button">
            <Button primary>더 알아보기</Button>
            <Button>버튼</Button>
          </div>
        </div>
        <div className="hero-search">
          <Card>
            <div className="hero-card-container">
              <form>
                <div className="search-label">
                  <span role="img" aria-label="search">
                    🔎
                  </span>
                  일홉찾기
                </div>
                <Input placeholder="위치" />
                <Input type="date" placeholder="날짜" />
                <Input placeholder="태그" />
                <Button primary>검색</Button>
              </form>
            </div>
          </Card>
        </div>
      </div>
    </div>
  );
};

export default Hero;
